import { defineStore } from "pinia";
import { ref } from "vue";

export const useProgressStore = defineStore("progressStore", () => {
  var currentStep = ref(1);
  const stepConditions = ref({
    // test
    step1: true,
    step2: false,
    step3: false,
    step4: false
  });

  function setStepCondition(step, status) {
    stepConditions.value[step] = status;
    currentStep = step.replace(/\D/g, "");
  }

  function isStepCompleted(step) {
    return stepConditions.value[step] || false; //  false 
  }

  function allstepCondition(){
    return stepConditions;

  }

  return { currentStep, stepConditions, setStepCondition, isStepCompleted, allstepCondition };
});