<template>
  <aside>
    <div class="logo">
      <img :src="logoURL" alt="Vue" />
    </div>

    <div class="menu">
      <div
        v-for="step in steps"
        :key="step.path"
        class="button"
        :class="{
          active: isActive(step.path), //   Step 
          completed: isCompleted(step.key), //   Step 
        }"
        @click="navigateToStep(step.path, step.key)"
      >
        <span class="material-icons">{{ step.label }}</span>
        <span class="text">{{ step.name }}</span>
      </div>
    </div>
  </aside>

    <v-dialog v-model="showDialog" max-width="400px">
    <v-card>
      <v-card-title>  </v-card-title>
      <v-card-text> {{ dialogText }} </v-card-text>
      <v-card-actions>
        <v-btn color="primary" @click="showDialog = false"></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script setup>
import { computed, ref } from "vue";
import { useRoute, useRouter } from "vue-router";
import { useProgressStore } from "@/store/progressStore";
import logoURL from "../assets/mrtLogo.png";

const route = useRoute();
const router = useRouter();
const progressStore = useProgressStore();

var dialogText = ref("");
const showDialog = ref(false);

const steps = [
  { path: "/step1", name: " ", label: "STEP1", key: "step1" },
  { path: "/step2", name: " ", label: "STEP2", key: "step2" },
  { path: "/step3", name: "", label: "STEP3", key: "step3" },
  { path: "/step4", name: "", label: "STEP4", key: "step4" },
];

//   Step 
const isActive = (path) => route.path === path;

//   Step  (computed )
const isCompleted = (stepKey) =>
  computed(() => progressStore.isStepCompleted(stepKey)).value;

//  Step    ( Step  )
const navigateToStep = (path, stepKey) => {
  if (progressStore.isStepCompleted(stepKey) || path === "/step1") {
    router.push(path);
  } else {
    dialogText = " Step    .";
    showDialog.value = true;
  }
};
</script>

<style lang="scss" scoped>
aside {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--background-light);
  color: var(--text-dark);
  height: var(--header-size);
  padding: 0 10%;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
  border-bottom: 2px solid var(--primary-color);

  .logo {
    display: flex;
    align-items: center;

    img {
      margin-left: 40px;
      width: 120px;
      height: auto;
    }
  }

  .menu {
    display: flex;
    gap: 50px;
    margin-left: auto;
    margin-right: auto;
    height: 80%;

    .button {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 8px;
      text-decoration: none;
      padding: 12px 20px;
      border-radius: 8px;
      font-size: 1rem;
      font-weight: bold;
      position: relative;
      background: none;
      cursor: not-allowed; //   
      transition: all 0.3s ease-in-out;
      border: 2px solid transparent;

      .material-icons {
        font-size: 1.3rem;
        transition: all 0.3s ease;
        color: var(--primary-color);
      }

      /*   Step  */
      &.active {
        color: #007bff;
        font-weight: bold;
        font-size: 20px;
        cursor: default;

        &::after {
          content: "";
          position: absolute;
          bottom: -4px;
          left: 50%;
          width: 50%;
          height: 3px;
          background: #007bff;
          transform: translateX(-50%);
          border-radius: 2px;
          transition: width 0.3s ease-in-out;
        }

        .material-icons {
          color: #007bff;
        }
      }

      /*   Step  ( ) */
      &.completed {
        color: #007bff;
        font-weight: bold;
        font-size: 20px;
        cursor: pointer;

        .material-icons {
          color: #007bff;
        }

        &:hover {
          transform: scale(1.08);
        }
      }
    }
  }
}
</style>