import { createRouter, createWebHashHistory } from "vue-router";
import { useProgressStore } from "@/store/progressStore";
import { nextTick } from "vue";

import Step1 from "../views/Step1View.vue";
import Step2 from "../views/Step2View.vue";
import Step3 from "../views/Step3View.vue";
import Step4 from "../views/Step4View.vue";

const routes = [
  { path: "/", redirect: "/step1" },
  { path: '/:catchAll(.*)', redirect: '/step1' },
  { path: "/step1", name: "step1", component: Step1, meta: { step: 1 } },
  { path: "/step2", name: "step2", component: Step2, meta: { step: 2 } },
  { path: "/step3", name: "step3", component: Step3, meta: { step: 3 } },
  { path: "/step4", name: "step4", component: Step4, meta: { step: 4 } },
];

const router = createRouter({
  history: createWebHashHistory('/nwas.github.io/'),
  routes,
});

//   Step    Step  
router.beforeEach(async (to, from, next) => {
  await nextTick(); // Pinia    
  const progressStore = useProgressStore(); //  Pinia  

  const toStep = to.meta.step;   //  Step 
  const fromStep = from.meta.step; //  Step 

  if (typeof toStep === "number" && typeof fromStep === "number" && toStep < fromStep) {
    console.log(` Step${fromStep}  Step${toStep}    Step `);
    
    for (let i = fromStep; i > toStep; i--) {
      const stepKey = `step${i}`;
      console.log(` ${stepKey}  `);
      progressStore.setStepCondition(stepKey, false);
    }
  }

  next();
});

export default router;