<template>
  <div class="step_container">
    <div class="listArea">
      <v-row>
        <v-col class="topology">
          <v-card>
            <v-card-title>Topology</v-card-title>

            <v-card-text class="mt-3">
              <p class="text-body-1">
                <strong> :</strong> {{ maxTopology }} 
              </p>

              <p class="text-body-1">
                <strong> :</strong> {{ comparison }} 
              </p>
            </v-card-text>
          </v-card>
        </v-col>

        <v-col class="topologyList">
          <v-card>
            <v-card-title class="d-flex justify-space-between align-center">
              TopologyList
            </v-card-title>

            <v-card-text class="text-body-1">
              <div v-if="topologyList.length > 0">
                <ul class="topology-ul">
                  <li
                    v-for="(list, index) in topologyList"
                    :key="index"
                    class="topology-item"
                    draggable="true"
                    @dragstart="dragStart(index)"
                    @dragover.prevent
                    @drop="dropItem(index)"
                  >
                    <div class="topology-info">
                      <v-icon class="drag-handle">mdi-drag</v-icon>
                      <!--   -->
                      <span
                        :style="{
                          display: 'inline-block',
                          padding: '3px 15px',
                          marginRight: '10px',
                          borderRadius: '5px',
                          fontSize: '12px',
                          fontWeight: 'bold',
                          color: textColors[index % textColors.length],
                          backgroundColor: colors[index % colors.length],
                        }"
                        >List {{ index + 1 }}.
                      </span>

                      <!--      -->

                      <p
                        v-for="(node, nodeIndex) in list || []"
                        :key="node.uniqueId || nodeIndex"
                      >
                        {{ node.label }}
                        <span v-if="nodeIndex < list.length - 1"> > </span>
                      </p>
                    </div>

                    <!--     -->
                    <div class="topology-actions">
                      <v-btn color="red" icon @click="deleteTopology(index)">
                        <v-icon size="xs">mdi-delete</v-icon>
                      </v-btn>
                    </div>
                  </li>
                </ul>
              </div>
              <p v-else class="text-body-1"> Topology .</p>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </div>

    <div class="arrangeArea">
      <div class="title">
        <p class="guide-message">
            : <strong>{{ getNextZoneName() }}</strong>
        </p>

        <v-btn class="addList" @click="addList" color="primary">  </v-btn>
      </div>

      <div class="drop-zones">
        <div
          v-for="zone in dropZones"
          :key="zone.id"
          class="drop-zone"
          :data-id="zone.id"
          :class="{
            'invalid-zone': !zone.isValid,
            'active-zone': zone.id === nextZoneId, //  
          }"
        >
          <h3>{{ zone.name }}</h3>

          <ul class="list-unstyled m-0 p-0">
            <li v-for="item in zone.items" :key="item.uniqueId || item.id">
              <div
                class="round Item"
                ref="buttons"
                @click="connectButton($event, item.id, zone.id, item)"
                :class="{
                  'next-clickable': zone.id === nextZoneId,
                }"
              >
                <div
                  class="header"
                  :class="[
                    { 'blue-header': item.setProperty },
                    `header-${item.uniqueId}`, //    
                  ]"
                >
                  {{ item.label }}
                </div>
                <div class="content">
                  <button
                    class="childButton"
                    :class="{ 'gray-button': item.id === 1 }"
                    @click.stop="openDialog(item.id, item.uniqueId, item.label)"
                  >
                    
                  </button>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="step-controls">
        <v-btn class="step-button" @click="goToNextStep">  </v-btn>
      </div>
    </div>

    <!-- SVG  ( ) -->

    <svg class="connection-svg">
      <!--    () -->
      <line
        v-for="(line, index) in pendingLines"
        :key="'pending-' + index"
        :x1="line.x1"
        :y1="line.y1"
        :x2="line.x2"
        :y2="line.y2"
        stroke="black"
        stroke-width="2"
        stroke-dasharray="0"
      />
      <!--   () -->
      <line
        v-for="(line, index) in completedLines"
        :key="'completed-' + index"
        :x1="line.x1"
        :y1="line.y1"
        :x2="line.x2"
        :y2="line.y2"
        stroke="black"
        stroke-width="2"
        stroke-dasharray="5,5"
      />
    </svg>

    <v-dialog v-model="showDialog" max-width="400px">
      <v-card>
        <v-card-title>  </v-card-title>
        <v-card-text> {{ dialogText }} </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click="showDialog = false"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>



<script setup>
import { useDropZoneStore } from "../store/dropZoneStore.js";
import { useTopologyStore } from "../store/topologyStore.js";
import { useProgressStore } from "../store/progressStore.js";
import { useRouter, onBeforeRouteLeave } from "vue-router";

import { storeToRefs } from "pinia";
import {
  ref,
  onMounted,
  onUnmounted,
  computed,
  defineEmits,
  watch,
  nextTick,
} from "vue";

const store = useDropZoneStore();
const topologyStore = useTopologyStore();
const progressStore = useProgressStore();
const router = useRouter();

const { dropZones } = storeToRefs(store);
const { topologyList } = storeToRefs(topologyStore);

//  
const clickedButtons = ref([]);
const completedLines = ref([]); //  ( )
const pendingLines = ref([]); //  (  )
//  
const allowedZoneOrder = [1, 2, 3, 4, 5, 6, 7]; //  Zone  
let currentZoneIndex = ref(0); //  Zone  

//  
const listItem = ref([]);
var dialogText = ref("");
const showDialog = ref(false);
const maxTopology = ref(0);
const comparison = computed(() => topologyStore.topologyList.length);

const colors = ["red", "orange", "yellow", "green", "blue", "indigo", "violet"];
const textColors = [
  "white",
  "white",
  "black",
  "white",
  "white",
  "white",
  "white",
];
const defaultButtonColor = "var(--item-header-bgcolor)"; //  
const defaultTextColor = "var(--item-header-textcolor)";

const emit = defineEmits(["openDialog"]);

const openDialog = (id, uniqueId, label) => {
  emit("openDialog", id, uniqueId, label);
};

watch(
  () =>
    dropZones.value.map((zone) => zone.items.map((item) => item.setProperty)),
  () => {
    nextTick(() => {
      resetButtons(); //       
    });
  },
  { deep: true }
);

watch(
  () =>
    topologyStore.topologyList
      .flat()
      .map((node) => `${node.uniqueId}:${node.setProperty}`)
      .join("|"),
  () => {
    console.log("   !");
  }
);

const resetButtons = () => {
  dropZones.value.forEach((zone) => {
    zone.items.forEach((item) => {
      const header = document.querySelector(`.header-${item.uniqueId}`);
      if (!header) return;

      if (item.setProperty) {
        // setProperty true   
        header.style.backgroundColor = "#0056b3";
        return;
      }

      header.style.backgroundColor = defaultButtonColor;
      header.style.color = defaultTextColor;
    });
  });
};

const clearLines = (event) => {
  if (event.key === "Escape") {
    pendingLines.value = []; //   () 

    clickedButtons.value = [];
    currentZoneIndex.value = 0;
    listItem.value = [];
    resetButtons();
  }

  if (event === "finish") {
    clickedButtons.value = [];
    currentZoneIndex.value = 0;
    listItem.value = [];
    resetButtons();
  }

  resetButtons();
};

//   
const calculateMaxTopology = () => {
  //  765(Zone ID: 2)   
  const relevantZones = dropZones.value.filter((zone) => zone.id !== 2);
  const allFilled = relevantZones.every((zone) => zone.items.length > 0);

  if (allFilled) {
    maxTopology.value = dropZones.value.reduce(
      (product, zone) => product * (zone.items.length || 1), //  765  1 
      1
    );
  } else {
    maxTopology.value = 0;
  }
};

//    ESC    
onMounted(() => {
  window.addEventListener("keydown", clearLines);
  //  
  calculateMaxTopology();
  console.log("Step2 mount!", topologyStore.topologyList);
});

onBeforeRouteLeave(() => {
  // test 
  // topologyStore.clearTopologyList();
});

//       (  )
onUnmounted(() => {
  window.removeEventListener("keydown", clearLines);
});

const connectButton = (event, buttonId, zoneId, item) => {
  const button = event.currentTarget;
  console.log("click", button);
  const rect = button.getBoundingClientRect();

  // 1 zone   
  if (clickedButtons.value.length === 0 && zoneId !== 1) {
    dialogText = " !";
    showDialog.value = true;
    highlightInvalidZone(zoneId);
    return;
  }

  if (zoneId === 1 && topologyStore.isNotAdd()) {
    dialogText = "    .";
    showDialog.value = true;
    return;
  }

  const zone765 = dropZones.value.find((zone) => zone.id === 2);
  const zone765HasItems = zone765 && zone765.items.length > 0;
  const previousZone = allowedZoneOrder[currentZoneIndex.value - 1];

  //  765kV(2)    
  const isSkippingZone765 =
    !zone765HasItems && previousZone === 1 && zoneId === 3;

  // 4 7 zone    
  const isMultiSelectableZone = [4, 7].includes(zoneId);

  //   zone     
  const isNextZone = allowedZoneOrder[currentZoneIndex.value] === zoneId;
  const isContinuingMultiSelect =
    isMultiSelectableZone &&
    clickedButtons.value.some((btn) => btn.zoneId === zoneId);

  //  22.9kV(5)  ,  6()   
  const lastSelectedZone =
    clickedButtons.value.length > 0
      ? clickedButtons.value[clickedButtons.value.length - 1].zoneId
      : null;
  const isValidTransition =
    (isNextZone || isSkippingZone765 || isContinuingMultiSelect) &&
    !(lastSelectedZone === 5 && zoneId !== 6); // 5(22.9)   6()  

  if (isValidTransition) {
    const x = rect.left + rect.width / 2;
    const y = rect.top + rect.height / 2;

    if (
      !clickedButtons.value.some((btn) => btn.id === buttonId) ||
      isMultiSelectableZone
    ) {
      clickedButtons.value.push({ id: buttonId, zoneId, x, y });
    }

    if (clickedButtons.value.length > 1) {
      const lastIndex = clickedButtons.value.length - 1;
      pendingLines.value.push({
        x1: clickedButtons.value[lastIndex - 1].x,
        y1: clickedButtons.value[lastIndex - 1].y,
        x2: clickedButtons.value[lastIndex].x,
        y2: clickedButtons.value[lastIndex].y,
      });
    }

    if (!listItem.value.includes(item)) {
      listItem.value.push(item);
    }

    button.querySelector(".header").style.backgroundColor =
      colors[topologyStore.topologyList.length];
    button.querySelector(".header").style.color =
      textColors[topologyStore.topologyList.length];

    // Zone    
    if (
      !isMultiSelectableZone ||
      (isMultiSelectableZone && !isContinuingMultiSelect)
    ) {
      if (isSkippingZone765) {
        currentZoneIndex.value += 2;
      } else {
        currentZoneIndex.value++;
      }
    }

    console.log(currentZoneIndex.value);
  } else {
    dialogText = " !";
    showDialog.value = true;
    highlightInvalidZone(zoneId);
    return;
  }
};

const addList = () => {
  if (
    listItem.value.length > 0 &&
    listItem.value[listItem.value.length - 1].id === 7
  ) {
    dialogText = "  ";
    showDialog.value = true;
    completedLines.value.push(...pendingLines.value);
    pendingLines.value = [];
    topologyStore.addTopology([...listItem.value]);
    clearLines("finish");
  } else {
    dialogText = " NWAs  !";
    showDialog.value = true;
  }

  console.log("step2 : dropZones", JSON.stringify(dropZones.value));
  console.log(
    "step2 : topologyStore",
    JSON.stringify(topologyStore.topologyList)
  );

  dropZones.value = structuredClone(dropZones.value);
};

//   zone   
const highlightInvalidZone = (zoneId) => {
  const zoneElement = document.querySelector(`.drop-zone[data-id="${zoneId}"]`); //    

  if (zoneElement) {
    zoneElement.classList.add("error-zone");
    setTimeout(() => {
      zoneElement.classList.remove("error-zone");
    }, 1000);
  } else {
    console.error(`  zoneId: ${zoneId}`); //    
  }
};

const getNextZoneName = () => {
  const nextZone = dropZones.value.find(
    (zone) => zone.id === allowedZoneOrder[currentZoneIndex.value]
  );
  return nextZone ? nextZone.name : "";
};

const nextZoneId = computed(() => {
  return allowedZoneOrder[currentZoneIndex.value]; //  zone ID 
});

//     
const draggedItemIndex = ref(null);

//   (  )
const dragStart = (index) => {
  draggedItemIndex.value = index;
};

//     
const dropItem = (index) => {
  if (draggedItemIndex.value === null) return;

  const topologyStore = useTopologyStore(); // Store 

  //      
  const updatedList = [...topologyStore.topologyList];
  const draggedItem = updatedList.splice(draggedItemIndex.value, 1)[0];
  updatedList.splice(index, 0, draggedItem);

  // Store  ( )
  topologyStore.setTopologyList(updatedList);

  draggedItemIndex.value = null;
};

//   
const deleteTopology = (index) => {
  const topologyStore = useTopologyStore();
  topologyStore.removeTopology(index); //  Store   
};

// ""      + 
const goToNextStep = () => {
  if (isStep2Completed()) {
    //   
    progressStore.setStepCondition("step2", true);
    router.push("/step3"); // Step3 
  } else {
    progressStore.setStepCondition("step2", false);
  }
};
//   
const isStep2Completed = () => {
  console.log("step2 : dropZones", JSON.stringify(dropZones.value));
  console.log(
    "step2 : topologyStore",
    JSON.stringify(topologyStore.topologyList)
  );

  if (topologyStore.topologyList.length < 2) {
    // topologyList   2 
    dialogText = "2  List !";
    showDialog.value = true;
    return false;
  }

  const temp = topologyStore.topologyList.every((group) =>
    group.every((node) => node.setProperty)
  );

  topologyStore.topologyList.forEach((group, groupIdx) => {
    group.forEach((node) => {
      if (!node.setProperty) {
        console.warn(
          ` Group ${groupIdx}, Node ${node.label} (ID: ${node.id})    `
        );

        console.log(JSON.stringify(node));
      }
    });
  });
  

  console.log(JSON.stringify(dropZones))

  


  if (!temp) {
    dialogText = "   ";
    showDialog.value = true;
    return false;
  }

  return true; //    true 
};
</script>





<style lang="scss" scoped>
.step_container {
  width: 1600px;
}

.arrangeArea {
  position: relative;
  overflow: visible;
  height: 80%;

  .title {
    display: flex;

    .guide-message {
      width: 1420px;
      text-align: center;
      font-size: 18px;
      font-weight: bold;
      color: #007bff;
      margin: 10px;
    }

    .addList {
      align-self: center;
    }
  }

  .drop-zones {
    gap: 40px;
  }
}

.listArea {
  padding: 0px 22px;
  height: 200px;
  width: 100%;

  .v-row,
  .v-col,
  .v-card {
    height: 100%;
  }

  .topology {
    flex: 0 0 200px;
    flex-shrink: 0;
  }

  .topologyList {
    flex-grow: 1;
    margin-left: 20px;
    max-width: 1300px;
  }

  .topology-ul {
    list-style: none;
    padding: 0;
    margin: 0;
    overflow-y: auto;
    height: 110px;

    .topology-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 8px 12px;
      border-bottom: 1px solid #ccc;
      background: white;
      transition: background 0.3s ease;

      .topology-info {
        display: flex;
        align-items: center;

        p {
          margin: 0px;
          width: 80px;
          display: flex;
          justify-content: space-around;

          span {
            margin: 0px 5px;
          }
        }
      }
    }
  }

  .draggable-item {
    cursor: grab;
  }

  .drag-handle {
    cursor: grab;
    font-size: 24px;
    margin-right: 10px;
  }

  .topology-actions {
    .v-btn {
      background-color: #ff5252 !important; //     ()
      width: 28px !important; //   
      height: 28px !important;
      min-width: 28px !important;
      border-radius: 50% !important; //   
      display: flex !important;
      align-items: center !important;
      justify-content: center !important;

      //   hover  
      &:hover {
        background-color: darken(
          #ff5252,
          10%
        ) !important; //     
      }

      //    
      &.v-btn--disabled {
        background-color: lighten(
          #ff5252,
          20%
        ) !important; //    
      }

      //    
      .v-icon {
        font-size: 14px !important; //   
        color: white !important; //  
      }
    }
  }
}

.connection-svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1; /*     */
  height: 100vh; /*        */
  pointer-events: none; /*     */
  z-index: 0; /*  drop-zone    */
}

.round {
  position: relative;
  z-index: 2; /*      */
}

.active-zone {
  border: 3px solid #007bff !important; /*    */
  background-color: rgba(0, 123, 255, 0.1); /*    */
  transition: 0.3s ease-in-out;
}

.next-clickable {
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
  }
  50% {
    box-shadow: 0 0 15px rgba(0, 123, 255, 0.8);
  }
  100% {
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
  }
}

.error-zone {
  animation: shake 0.3s ease-in-out;
  border: 2px solid red !important;
}

@keyframes shake {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-5px);
  }
  50% {
    transform: translateX(5px);
  }
  75% {
    transform: translateX(-5px);
  }
  100% {
    transform: translateX(0);
  }
}
</style>