<template>
  <div class="step_container">
    <v-container class="treeArea">
      <div class="topologyList">
        <span
          v-for="(list, index) in topologyList"
          :key="index"
          @click="highlightList(index)"
          :style="{
            display: 'inline-block',
            padding: '3px 15px',
            marginRight: '10px',
            borderRadius: '5px',
            fontSize: '12px',
            fontWeight: 'bold',
            cursor: 'pointer',
            border:
              highlightedIndex === index
                ? `2px solid ${colors[index % colors.length]}`
                : 'none',
            color: textColors[index % textColors.length],
            backgroundColor: colors[index % colors.length],
          }"
        >
          List {{ index + 1 }}
        </span>
      </div>

      <div
        v-for="(zone, zoneIndex) in orderedZones"
        :key="zoneIndex"
        class="zone-column"
      >
        <p class="zone-title">{{ zone.name }}</p>
        <div class="vertical-layout">
          <button
            v-for="node in zone.nodes"
            :key="node.uniqueId"
            class="node-button"
            :class="{
              'node-disabled':
                node.label === '' || node.uniqueId === nextNodeId,
              'node-selected': isNodeInSelectedList(node), //     
              'node-active':
                isNodeInSelectedList(node) && node.lat !== 0 && node.lng !== 0, //     
            }"
            @click="
              isNodeInSelectedList(node) && node.label !== ''
                ? btnCoordinates(node.uniqueId, node.label, node.id)
                : null
            "
            :data-zone-index="zoneIndex"
            :data-node-id="node.uniqueId"
            :style="{
              backgroundColor: isNodeInSelectedList(node)
                ? node.lat !== 0 && node.lng !== 0
                  ? colors[highlightedIndex % colors.length] //      
                  : '#2C3E50' //    
                : '#808080', //    
              color: isNodeInSelectedList(node)
                ? node.lat !== 0 && node.lng !== 0
                  ? textColors[highlightedIndex % colors.length] //      
                  : '#fff' //    
                : '#000', //    
              opacity: isNodeInSelectedList(node) ? 1 : 0.4, //    
            }"
            :disabled="!isNodeInSelectedList(node) || node.label === ''"
          >
            {{ node.label }}
          </button>
        </div>
      </div>

      <div class="step-controls">
        <v-btn class="step-button" @click="goToNextStep">  </v-btn>
      </div>
    </v-container>

    <svg class="connection-svg">
      <line
        v-for="(line, index) in computedLines"
        :key="index"
        :x1="line.x1"
        :y1="line.y1"
        :x2="line.x2"
        :y2="line.y2"
        :stroke="computedStrokeColors[index]"
        stroke-width="2"
        :class="{
          'highlighted-line': selectedLines.has(index),
          'dimmed-line': !selectedLines.has(index),
        }"
      />
    </svg>

    <div class="mapArea" id="map" ref="mapRef"></div>

    <v-dialog v-model="showDialog" max-width="400px">
      <v-card>
        <v-card-title>  </v-card-title>
        <v-card-text> {{ dialogText }} </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click="showDialog = false"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>


<script setup>
import { ref, nextTick, onMounted, computed } from "vue";
import { storeToRefs } from "pinia";
import { useTopologyStore } from "../store/topologyStore.js";
import { useProgressStore } from "../store/progressStore.js";
import { useRouter } from "vue-router";

const topologyStore = useTopologyStore();
const progressStore = useProgressStore();
const router = useRouter();

const { topologyList } = storeToRefs(topologyStore);

const buttons = ref([]);
const computedLines = ref([]);
const highlightedIndex = ref(0);
const nextNodeId = ref(null);

const selectedNodes = ref(new Set()); //     
const selectedLines = ref(new Set()); //     
// let markers = [];
let map;
let pathLines = []; //   Polyline 

var dialogText = ref("");
const showDialog = ref(false);

/**
 *  ZONE   
 */
const zoneOrder = [
  { id: 7, name: "NWAs" },
  { id: 6, name: "" },
  { id: 5, name: "22.9" },
  { id: 4, name: "154" },
  { id: 3, name: "345" },
  { id: 2, name: "765" },
  { id: 1, name: "" },
];

const colors = ["red", "orange", "yellow", "green", "blue", "indigo", "violet"];
const textColors = [
  "white",
  "white",
  "black",
  "white",
  "white",
  "white",
  "white",
];

onMounted(() => {
  nextTick(() => {
    updateLines();
  });

  // Google Maps API   
  const waitForGoogleMaps = setInterval(() => {
    if (window.google && window.google.maps) {
      clearInterval(waitForGoogleMaps);
      window.initMap();
    }
  }, 100);

  // 765 or 345 
  let found = false;

  topologyList.value.forEach((list) => {
    list.forEach((item) => {
      if (!found && item.id === 2) {
        nextNodeId.value = item.uniqueId;
        found = true; //    2  
      }
    });
  });

  if (!found) {
    topologyList.value.forEach((list) => {
      list.forEach((item) => {
        if (item.id === 3) {
          nextNodeId.value = item.uniqueId;
        }
      });
    });
  }
});

const isNodeInSelectedList = (node) => {
  return selectedNodes.value.has(node.uniqueId);
};

const orderedZones = computed(() => {
  // Zone   (ID  )
  // Zone  Map 
  const zoneMap = new Map(
    zoneOrder.map(({ id, name }) => [id, { name, nodes: [] }])
  );
  const seenIds = new Set(); //   Set

  // topologyList      Zone 
  topologyList.value.forEach((list) => {
    list.forEach((node) => {
      if (seenIds.has(node.uniqueId)) return; //  
      seenIds.add(node.uniqueId);

      //   ID  Zone 
      const zone = zoneMap.get(node.id) || zoneMap.get(7); // ID  NWAs 
      zone.nodes.push(node);
    });
  });

  //   Zone  ,  Zone 
  return zoneOrder.map(({ id }) => zoneMap.get(id));
});

/**
 *       
 */
const updateLines = async () => {
  computedLines.value = []; //    
  await nextTick(); //  DOM   

  requestAnimationFrame(() => {
    buttons.value = Array.from(document.querySelectorAll(".node-button"));

    if (buttons.value.length === 0) {
      return;
    }

    const buttonMap = new Map();

    //   uniqueId  
    buttons.value.forEach((btn) => {
      const zoneIndex = btn.getAttribute("data-zone-index");
      const nodeId = btn.getAttribute("data-node-id");

      if (!zoneIndex || !nodeId) {
        return;
      }

      buttonMap.set(`${zoneIndex}-${nodeId}`, btn);
    });

    //    
    topologyList.value.forEach((list, listIndex) => {
      list.forEach((node, nodeIndex) => {
        if (nodeIndex === list.length - 1) return; //     

        const nextNode = list[nodeIndex + 1];

        //     Zone 
        const currentZoneIndex = orderedZones.value.findIndex((zone) =>
          zone.nodes.some((zNode) => zNode.uniqueId === node.uniqueId)
        );

        const nextZoneIndex = orderedZones.value.findIndex((zone) =>
          zone.nodes.some((zNode) => zNode.uniqueId === nextNode.uniqueId)
        );

        if (currentZoneIndex === -1 || nextZoneIndex === -1) return;

        const startButton = buttonMap.get(
          `${currentZoneIndex}-${node.uniqueId}`
        );
        const endButton = buttonMap.get(
          `${nextZoneIndex}-${nextNode.uniqueId}`
        );

        if (!startButton || !endButton) {
          return;
        }

        const startRect = startButton.getBoundingClientRect();
        const endRect = endButton.getBoundingClientRect();

        computedLines.value.push({
          x1: startRect.left + startRect.width / 2,
          y1: startRect.top + startRect.height / 2 + window.scrollY,
          x2: endRect.left + endRect.width / 2,
          y2: endRect.top + endRect.height / 2 + window.scrollY,
          startNodeId: node.uniqueId,
          endNodeId: nextNode.uniqueId,
          listIndex: listIndex, //      listIndex 
        });
      });
    });
  });
};

//     
const highlightList = (index) => {
  highlightedIndex.value = index; //      
  selectedNodes.value.clear();
  selectedLines.value.clear();
  selectedButton.value = null; //     (  )

  if (topologyList.value[index]) {
    topologyList.value[index].forEach((node) => {
      selectedNodes.value.add(node.uniqueId);
    });

    computedLines.value.forEach((line, lineIndex) => {
      if (line.listIndex === index) {
        selectedLines.value.add(lineIndex);
      }
    });
  }

  markers.forEach((marker) => {
    const matchingNode = topologyList.value[index].find(
      (node) => node.uniqueId === marker.btnId
    );
    if (matchingNode) {
      marker.setIcon({
        ...svgMarker(colors[index % colors.length]), //    
        anchor: new window.google.maps.Point(12, 22),
        scaledSize: new window.google.maps.Size(24, 24),
      });
    } else {
      marker.setIcon({
        ...svgMarker("#808080"), //    
        anchor: new window.google.maps.Point(12, 22),
        scaledSize: new window.google.maps.Size(24, 24),
      });
    }
  });

  updatePolyline(); //      
  updatePopups(); //          
};

const computedStrokeColors = computed(() => {
  return computedLines.value.map((line, index) => {
    return selectedLines.value.has(index)
      ? colors[line.listIndex % colors.length] //      
      : "gray"; //   
  });
});

const loadGoogleMaps = () => {
  return new Promise((resolve, reject) => {
    if (window.google && window.google.maps) {
      resolve();
      return;
    }

    const script = document.createElement("script");
    script.src = `https://maps.googleapis.com/maps/api/js?key=AIzaSyBXEVH1qv_P54Ew7f7uiYtdK0YQ7h3c3Yg&libraries=geometry,places`;
    script.async = true;
    script.defer = true;
    script.onload = () => {
      resolve();
    };
    script.onerror = (error) => {
      reject(error);
    };

    document.head.appendChild(script);
  });
};

//  "" sig_cd 
const getSIG_CD = () => {
  if (!Array.isArray(topologyList.value)) return null;

  const flatNodes = topologyList.value.flat(); //  

  for (const item of flatNodes) {
    if (item.label !== "") continue; // ""  

    //     ( )
    return item;
  }

  return null;
};

let CustomPopup;

//    
window.initMap = async () => {
  await loadGoogleMaps();

  CustomPopup = class CustomPopup extends (
    (window.google?.maps?.OverlayView || class {})
  ) {
    constructor(position, content, id) {
      super();
      if (!window.google || !window.google.maps) {
        throw new Error("Google Maps API  .");
      }

      this.position = position;
      this.id = id;

      this.containerDiv = document.createElement("div");
      this.containerDiv.className = "custom-popup";
      this.containerDiv.style.position = "absolute";
      this.containerDiv.style.display = "none"; //  
      this.containerDiv.style.transform = "translate(-50%, -150%)"; //    
      this.containerDiv.style.backgroundColor = "white";
      this.containerDiv.style.boxShadow = "0px 2px 10px rgba(0,0,0,0.3)";
      this.containerDiv.style.borderRadius = "8px";
      this.containerDiv.style.whiteSpace = "nowrap";

      this.containerDiv.innerHTML = `
      <div class="custom-popup-header" style="font-weight: bold; display: flex; justify-content: space-between; align-items: center;">
         ${content.title}
        <span class="custom-popup-close" style="cursor: pointer;">&times;</span>
      </div>
      <div class="custom-popup-body" style="margin-top: 5px;">${content.body}</div>
    `;

      //    
      const closeBtn = this.containerDiv.querySelector(".custom-popup-close");
      closeBtn?.addEventListener("click", () => {
        this.hide();
      });
    }

    onAdd() {
      this.getPanes().floatPane.appendChild(this.containerDiv); // `floatPane`    
      this.show(); //   
    }

    draw() {
      const projection = this.getProjection();
      if (!projection) return;

      const pos = projection.fromLatLngToDivPixel(this.position);
      if (pos) {
        this.containerDiv.style.left = `${pos.x}px`;
        this.containerDiv.style.top = `${pos.y - 15}px`; //   
      }
    }

    onRemove() {
      if (this.containerDiv.parentElement) {
        this.containerDiv.parentElement.removeChild(this.containerDiv);
      }
    }

    show() {
      this.containerDiv.style.display = "block";
    }

    hide() {
      this.containerDiv.style.display = "none";
    }
  };

  //     
  const item = getSIG_CD();
  if (!item) return;

  const addressObj = item.property?.find((p) => p.name === "");
  const address = addressObj?.values?.[0]?.data || null;

  const coordinates = await getCoordinatesFromAddress(address);

  //  
  const mapElement = document.getElementById("map");
  if (!mapElement) return;

  map = new window.google.maps.Map(mapElement, {
    zoom: 3,
    mapTypeId: "hybrid",

    styles: [
      { elementType: "labels.icon", stylers: [{ visibility: "off" }] },
      { elementType: "labels.text.fill", stylers: [{ color: "#ffffff" }] },
      { elementType: "labels.text.stroke", stylers: [{ color: "#000000" }] },
    ],
  });

  //    
  const bounds = new window.google.maps.LatLngBounds();
  bounds.extend(coordinates);
  map.fitBounds(bounds);

  setCoordinates(
    item.uniqueId,
    bounds.getCenter().lat(),
    bounds.getCenter().lng()
  );

  window.google.maps.event.addListenerOnce(map, "bounds_changed", () => {
    map.setZoom(12);
  });

  //   
  const marker = new window.google.maps.Marker({
    position: bounds.getCenter(),
    map: map,
    icon: {
      ...svgMarker("#f00"),
      anchor: new window.google.maps.Point(12, 22), //    
      scaledSize: new window.google.maps.Size(24, 24), //  
    },
  });

  //   
  const customPopup = new CustomPopup(bounds.getCenter(), {
    title: "",
    body: `
      : ${bounds.getCenter().lat()}<br>
      : ${bounds.getCenter().lng()}
    `,
  });

  customPopup.setMap(map);

  //    
  marker.addListener("click", () => {
    customPopup.show();
  });

  //   
  customPopup.show();

  map.addListener("click", (event) => {
    addMarker(event.latLng);
  });
};

const svgMarker = (color) => ({
  path: "M12,11.5A2.5,2.5 0 0,1 9.5,9A2.5,2.5 0 0,1 12,6.5A2.5,2.5 0 0,1 14.5,9A2.5,2.5 0 0,1 12,11.5M12,2A7,7 0 0,0 5,9C5,14.25 12,22 12,22C12,22 19,14.25 19,9A7,7 0 0,0 12,2Z",
  fillColor: color,
  fillOpacity: 1,
  strokeWeight: 1,
  strokeColor: "#FFFFFF",
  scale: 2,
});

const getCoordinatesFromAddress = (address) => {
  return new Promise((resolve, reject) => {
    let geocoder = new window.google.maps.Geocoder();

    geocoder.geocode({ address }, (results, status) => {
      if (status === "OK" && results[0]) {
        const location = results[0].geometry.location;
        resolve({ lat: location.lat(), lng: location.lng() });
      } else {
        reject(status);
      }
    });
  });
};

//  uniqueId   
const setCoordinates = (uniqueId, lat, lng) => {
  if (!Array.isArray(topologyList.value)) return;

  topologyList.value = topologyList.value.map((group) =>
    group.map((item) =>
      item.uniqueId === uniqueId
        ? { ...item, lat, lng } //    Vue   
        : item
    )
  );
};

const mapRef = ref(null);
let markers = [];
let popups = [];

const selectedButton = ref(null);

const btnCoordinates = (uniqueId, label, id) => {
  selectedButton.value = { uniqueId: uniqueId, label: label, id: id };
};

const addMarker = (latLng) => {
  if (selectedButton.value === null) return; //    

  const newCoord = { lat: latLng.lat(), lng: latLng.lng() };

  //      
  const groupIndex = topologyList.value.findIndex((group) =>
    group.some((item) => item.uniqueId === selectedButton.value.uniqueId)
  );

  if (groupIndex === -1) {
    console.error("      .");
    return;
  }

  //     
  const selectedColor =
    highlightedIndex.value !== null
      ? colors[highlightedIndex.value % colors.length] //    
      : colors[groupIndex % colors.length]; //    

  //     (    )
  topologyList.value = topologyList.value.map((group) =>
    group.map((item) =>
      item.uniqueId === selectedButton.value.uniqueId
        ? { ...item, lat: newCoord.lat, lng: newCoord.lng }
        : item
    )
  );

  //  id === 1     

if (selectedButton.value.id === 1) {
  const allNodes = topologyList.value.flat();

  const currentIndex = allNodes.findIndex(
    (item) => item.uniqueId === selectedButton.value.uniqueId
  );

  const nextNode = allNodes[currentIndex + 1];

  if (nextNode) {
    console.log("nextNode  ", nextNode.label);

    // nextNode uniqueId      
    topologyList.value.forEach((group) => {
      group.forEach((node) => {
        if (node.uniqueId === nextNode.uniqueId) {
          node.lat = newCoord.lat;
          node.lng = newCoord.lng;
        }
      });
    });

    // UI nextNodeId 
    nextNodeId.value = nextNode.uniqueId;
  } else {
    console.log("nextNode ");
  }
}

  //     ( )
  let markerExists = false;
  markers.forEach((marker) => {
    if (marker.btnId === selectedButton.value.uniqueId) {
      marker.setIcon({
        ...svgMarker(selectedColor), //      
        anchor: new window.google.maps.Point(12, 22),
        scaledSize: new window.google.maps.Size(24, 24),
      });
      marker.setPosition(newCoord); //  
      markerExists = true;
    }
  });

  //      
  if (!markerExists) {
    const marker = new window.google.maps.Marker({
      position: newCoord,
      map: map,
      title: `Marker for ${selectedButton.value.uniqueId}`,
      icon: {
        ...svgMarker(selectedColor), //    
        anchor: new window.google.maps.Point(12, 22),
        scaledSize: new window.google.maps.Size(24, 24),
      },
    });
    marker.btnId = selectedButton.value.uniqueId;
    markers.push(marker);
  }

  //     ( )
  popups = popups.filter((popup) => {
    if (popup.btnId === selectedButton.value.uniqueId) {
      popup.setMap(null); //   
      return false;
    }
    return true;
  });

  //    
  const customPopup = new CustomPopup(newCoord, {
    title: selectedButton.value.label,
    id: selectedButton.value.uniqueId,
    body: `
      : ${newCoord.lat}<br>
      : ${newCoord.lng}
    `,
  });

  customPopup.btnId = selectedButton.value.uniqueId; //   ID 
  customPopup.setMap(map);
  popups.push(customPopup);

  //      
  markers.forEach((marker) => {
    marker.addListener("click", () => {
      popups.forEach((popup) => {
        if (popup.btnId === marker.btnId) {
          popup.show();
        } else {
          popup.hide(); //   
        }
      });
    });
  });

  //      
  updatePopups();
  updatePolyline();

  console.log(topologyList.value);
};

//     Polyline  
const updatePolyline = () => {
  if (!map) {
    console.error(" (map)   .");
    return;
  }

  //  Polyline  (  )
  pathLines.forEach((polyline) => polyline.setMap(null));
  pathLines = [];

  //       
  if (highlightedIndex.value === null) return;

  //    
  const selectedGroup = topologyList.value[highlightedIndex.value];
  if (!selectedGroup) return;

  let validCoordinates = [];

  //       
  selectedGroup.forEach((node) => {
    if (
      Number.isFinite(node.lat) &&
      Number.isFinite(node.lng) &&
      node.lat !== 0 &&
      node.lng !== 0
    ) {
      validCoordinates.push({ lat: node.lat, lng: node.lng });
    }

    //   uniqueId       
    topologyList.value.forEach((otherGroup) => {
      otherGroup.forEach((otherNode) => {
        if (
          otherNode.uniqueId === node.uniqueId && //  uniqueId  
          Number.isFinite(otherNode.lat) &&
          Number.isFinite(otherNode.lng) &&
          otherNode.lat !== 0 &&
          otherNode.lng !== 0
        ) {
          validCoordinates.push({ lat: otherNode.lat, lng: otherNode.lng });
        }
      });
    });
  });

  //   
  validCoordinates = Array.from(
    new Set(validCoordinates.map(JSON.stringify))
  ).map(JSON.parse);

  //   2    
  if (validCoordinates.length > 1) {
    const color = colors[highlightedIndex.value % colors.length]; //   
    const polyline = new window.google.maps.Polyline({
      path: validCoordinates,
      geodesic: true,
      strokeColor: color,
      strokeOpacity: 1.0,
      strokeWeight: 3,
    });

    polyline.setMap(map);
    pathLines.push(polyline);
  }
};

const updatePopups = () => {
  if (!map) {
    console.error(" (map)   .");
    return;
  }

  if (highlightedIndex.value === null) return; //    

  const selectedGroup = topologyList.value[highlightedIndex.value]; //   
  if (!selectedGroup) return;

  popups.forEach((popup) => {
    const matchingNode = selectedGroup.find(
      (node) => node.uniqueId === popup.btnId
    );

    if (matchingNode) {
      popup.setMap(map); //     
    } else {
      popup.setMap(null); //    
    }
  });
};

//  
const goToNextStep = () => {
  if (areAllCoordinatesSet(topologyList.value)) {
    progressStore.setStepCondition("step3", true);
    router.push("/step4");
  } else {
    dialogText = "   !";
    showDialog.value = true;
    progressStore.setStepCondition("step3", false);
  }

  console.log(JSON.stringify(topologyList.value));
};
//   
const areAllCoordinatesSet = (list) => {
  return list.every((group) =>
    group.every((node) => node.lat !== 0 && node.lng !== 0)
  );
};
</script>


<style lang="scss" scoped>
.step_container {
  display: flex;
  margin: 0px;
  padding: 0px;
  height: calc(100vh - var(--header-size));
  width: 100vw;
  overflow: hidden;

  .treeArea {
    width: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    gap: 40px;

    .zone-column {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 10px;
      background: rgba(0, 0, 255, 0.1);
      border-radius: 10px;
      z-index: 3;

      p {
        width: 15%;
        text-align: center;
        margin: auto;
      }

      .vertical-layout {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: space-around;
      }
    }
    .node-button {
      width: 120px;
      height: 50px;
      background-color: #808080; //   ()
      color: white;
      border: none;
      border-radius: 5px;
      text-align: center;
      cursor: pointer;
      margin: 0px 10px;
      transition: 0.3s;
      opacity: 1;

      &:hover {
        transform: scale(1.05);
      }

      &.node-selected {
        background-color: #2c3e50; //    
      }

      &.node-active {
        opacity: 1;
        pointer-events: auto;

        &:hover {
          filter: brightness(90%);
        }
      }

      &.node-disabled {
        pointer-events: none !important;

        &:hover {
          transform: none;
        }
      }

      &:disabled {
        cursor: not-allowed;
        opacity: 0.4;
      }
    }
  }
  .connection-svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
