<template>
  <div class="step_container">
    <div class="itemsArea">
      <v-row>
        <v-col>
          <v-card class="demandArea">
            <v-card-title></v-card-title>
            <v-card-text>
              <v-text-field
                label=" "
                variant="outlined"
                v-model="demandArea"
                placeholder=" "
                readonly
                prepend-inner-icon="mdi-magnify"
                @click="openPostcode"
                color="primary"
                class="mb-3"
                hide-details
              />

              <v-text-field
                label=""
                type="number"
                variant="outlined"
                v-model="electricCapacity"
                prepend-inner-icon="mdi-lightning-bolt"
                color="primary"
                class="mb-3"
                hide-details
              >
                <template #append-inner>
                  <span class="text-subtitle-2 pr-2">MW</span>
                </template>
              </v-text-field>

              <v-text-field
                label="  "
                type="number"
                variant="outlined"
                v-model="hoursPerDay"
                prepend-inner-icon="mdi-hours-24"
                color="primary"
                @update:model-value = 'validateHoursPerDay'
              >
                <template #append-inner>
                  <span class="text-subtitle-2 pr-2">hr/day</span>
                </template>
              </v-text-field>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>

      <v-row>
        <v-col>
          <v-card>
            <v-card-title></v-card-title>
            <div class="buttons">
              <v-btn
                class="round Item"
                v-for="btn in transStore"
                :key="btn.id"
                draggable="true"
                @dragstart="startDrag($event, btn)"
              >
                {{ btn.label }}
              </v-btn>
            </div>
            <v-divider></v-divider>
            <div class="buttons">
              <v-btn
                class="round Item"
                v-for="btn in distribStore"
                :key="btn.id"
                draggable="true"
                @dragstart="startDrag($event, btn)"
              >
                {{ btn.label }}
              </v-btn>
            </div>
          </v-card>
        </v-col>
      </v-row>

      <v-row>
        <v-col>
          <v-card>
            <v-card-title>NWAs</v-card-title>
            <div class="buttons nwas">
              <v-btn
                class="round Item"
                v-for="btn in nwasStore"
                :key="btn.id"
                draggable="true"
                @dragstart="startDrag($event, btn)"
              >
                {{ btn.label }}
              </v-btn>
            </div>
          </v-card>
        </v-col>
      </v-row>

      <v-row>
        <v-col>
          <v-card class="cntTopology">
            <v-card-title>Topology</v-card-title>
            <v-card-text class="mt-3">
              <p class="text-body-1">
                <strong> :</strong> {{ maxTopology }} 
              </p>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </div>

    <div class="arrangeArea">
      <div class="drop-zones">
        <div
          v-for="zone in dropZones"
          :key="zone.id"
          class="drop-zone"
          :class="{ 'invalid-zone': !zone.isValid }"
          @dragover="onDragOver($event, zone.id)"
          @dragleave="resetZoneColors"
          @drop="onDrop($event, zone.id)"
        >
          <h3>{{ zone.name }}</h3>
          <ul class="list-unstyled m-0 p-0">
            <li v-for="item in zone.items" :key="item.id">
              <div
                class="round Item"
                @contextmenu.prevent="removeItem(zone.id, item.uniqueId)"
              >
                <div
                  class="header"
                  :class="{ 'blue-header': item.setProperty }"
                >
                  {{ item.label }}
                </div>

                <div class="content">
                  <button
                    class="childButton"
                    :class="{ 'gray-button': item.id === 1 }"
                    @click="openDialog(item.id, item.uniqueId, item.label)"
                  >
                    
                  </button>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>

      <div class="step-controls">
        <v-btn class="step-button" @click="goToNextStep">  </v-btn>
      </div>
    </div>
  </div>

  <v-dialog v-model="showDialog" max-width="400px">
    <v-card>
      <v-card-title>  </v-card-title>
      <v-card-text> {{ dialogText }} </v-card-text>
      <v-card-actions>
        <v-btn color="primary" @click="showDialog = false"></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>


<script setup>
import { ref, watch, defineEmits } from "vue";
import { useDropZoneStore } from "../store/dropZoneStore.js";
import { useItemStore } from "../store/itemStore.js";
import { useProgressStore } from "../store/progressStore.js";
import { useTopologyStore } from "../store/topologyStore.js";
import { storeToRefs } from "pinia";
import { useRouter } from "vue-router";

const store = useDropZoneStore();
const topologyStore = useTopologyStore();

const { dropZones } = storeToRefs(store);

const itemStore = useItemStore();
const { transStore } = storeToRefs(itemStore);
const { distribStore } = storeToRefs(itemStore);
const { nwasStore } = storeToRefs(itemStore);

const startItem = ref(null);
const maxTopology = ref(0);

const progressStore = useProgressStore();
const router = useRouter();
const demandArea = ref("");
const electricCapacity = ref("");
const hoursPerDay = ref("");

var dialogText = ref("");
const showDialog = ref(false);

const emit = defineEmits(["openDialog"]);

const openDialog = (id, uniqueId, label) => {
  emit("openDialog", id, uniqueId, label);
};

// 
const openPostcode = () => {
  new window.daum.Postcode({
    oncomplete: function (data) {
      demandArea.value = data.address;
    },
  }).open();
};

//    
const startDrag = (event, item) => {
  event.dataTransfer.effectAllowed = "move";
  event.dataTransfer.setData("draggedItem", JSON.stringify(item));
  startItem.value = item;
};

//  
const onDrop = (event, zoneId) => {
  const draggedItem = JSON.parse(event.dataTransfer.getData("draggedItem"));

  store.addItemToZone(zoneId, draggedItem);
  resetZoneColors();
  startItem.value = null;
};

//   
const onDragOver = (event, zoneId) => {
  event.preventDefault();

  if (!startItem.value) return;

  const isValid = zoneId === startItem.value.id;

  event.dataTransfer.dropEffect = isValid ? "move" : "none";

  dropZones.value.forEach((zone) => {
    zone.isValid = zone.id === startItem.value.id;
  });
};

const resetZoneColors = () => {
  dropZones.value.forEach((zone) => (zone.isValid = true));
};

function validateHoursPerDay(value){
  if (value > 24){
    dialogText = "    24 .";
    showDialog.value = true;
    hoursPerDay.value = ref('')
  }
}

//     zone6  
watch(
  [demandArea, electricCapacity, hoursPerDay],
  ([newAddress, newCapacity, newHoursPerDay]) => {
    if (newAddress && newCapacity && newHoursPerDay) {
      store.saveDemandArea(newAddress, newCapacity, newHoursPerDay);
    }
  }
);

//  dropzone     
watch(
  dropZones,
  (newZones) => {
    //  765(Zone ID: 2)   Zone   
    const relevantZones = newZones.filter((zone) => zone.id !== 2);

    const allFilled = relevantZones.every((zone) => zone.items.length > 0);

    if (allFilled) {
      //  maxTopology = (765 )  zone items  
      maxTopology.value = newZones.reduce(
        (product, zone) => product * (zone.items.length || 1), //   zone 1 
        1
      );
    } else {
      maxTopology.value = 0;
    }
  },
  { deep: true, immediate: true }
);

const removeItem = (zoneId, uniqueId) => {
  store.removeItemFromZone(zoneId, uniqueId);
  topologyStore.clearTopologyList();
};

//   
const isStep1Completed = () => {
  // 1. DropZoneStore    (ID 6)
  const demandZone = dropZones.value.find((zone) => zone.id === 6);
  const hasDemandData = demandZone?.items?.length > 0; //    

  if (!hasDemandData) {
    dialogText = "   .";
    showDialog.value = true;
    return false;
  }

  // 2. 765   dropZone   
  const relevantZones = dropZones.value.filter((zone) => zone.id !== 2);
  const allFilled = relevantZones.every((zone) => zone.items.length > 0);

  if (!allFilled) {
    dialogText = " DropZone   . (765 )";
    showDialog.value = true;
    return false;
  }

  return true; //    true 
};

// ""      + 
const goToNextStep = () => {
  if (isStep1Completed()) {
    //   
    progressStore.setStepCondition("step1", true); // Step1   
    router.push("/step2"); // Step2 
    console.log("step1 DropZones : ", JSON.stringify(dropZones.value));
  } else {
    progressStore.setStepCondition("step1", false); // Step1   
  }
};
</script>


<style lang="scss" scoped>
.step_container {
  display: flex;
}

#daumLocation {
  margin-left: 16px;
  background: rgb(var(--dark-alt));
  border: none;
  border-radius: 2px;
  padding: 4px 8px;
  color: white;
  font-size: 14px;
  font-weight: bold;
}
</style>