<template>
  <v-dialog
    v-model="dialog"
    max-width="600px"
    persistent
    v-if="topologyData.length > 0"
  >
    <v-card>
      <v-card-title class="dialog-title">
        {{ label }}
      </v-card-title>

      <v-card-text>
        <v-table v-if="topologyData.length" class="styled-table">
          <thead>
            <tr>
              <th class="col-item group-header"></th>
              <th class="col-value group-header"></th>
              <th class="col-unit group-header"></th>
            </tr>
          </thead>
          <tbody>
            <template v-for="(item, index) in topologyData" :key="index">
              <template
                v-if="
                  isSimpleArray(item.values) &&
                  item.name !== '  ' &&
                  item.name !== ' '
                "
              >
                <tr>
                  <td :class="['td-item', `depth-${item.depth}`]">
                    {{ item.name }}
                  </td>
                  <td class="td-value">
                    <v-text-field
                      v-if="item.values[0].modify"
                      v-model="item.values[0].data"
                      dense
                      variant="plain"
                      type="number"
                      class="input-field"
                    ></v-text-field>
                    <span v-else class="blue-text">
                      {{ item.values[0].data }}
                    </span>
                  </td>
                  <td class="td-unit">{{ item.values[0]?.unit || "" }}</td>
                </tr>
              </template>

              <!-- 22.9     -->
              <!--     &     -->
              <template v-else-if="isConstructionCost(item)">
                <tr>
                  <th colspan="3" :class="['bold-text', `depth-${item.depth}`]">
                    {{ item.name }}
                  </th>
                </tr>

                <!--   : 229    (/ + AC/DC) -->
                <tr v-if="item.name === '  '">
                  <td colspan="3">
                    <v-radio-group
                      v-model="expansionType"
                      class="radio-group"
                      hide-details
                    >
                      <v-radio label="" value=""></v-radio>
                      <v-radio label="" value=""></v-radio>
                    </v-radio-group>
                  </td>
                </tr>
                <tr v-if="item.name === '  '">
                  <td colspan="3">
                    <v-radio-group
                      v-model="powerType"
                      class="radio-group"
                      hide-details
                    >
                      <v-radio label="AC" value="AC"></v-radio>
                      <v-radio label="DC" value="DC"></v-radio>
                    </v-radio-group>
                  </td>
                </tr>

                <!--   :     (/) -->
                <tr v-if="item.name === ' '">
                  <td colspan="3">
                    <v-radio-group
                      v-model="solarConstructionType"
                      class="radio-group"
                      hide-details
                    >
                      <v-radio label="" value=""></v-radio>
                      <v-radio label="" value=""></v-radio>
                    </v-radio-group>
                  </td>
                </tr>

                <!--      -->
                <template v-if="getFilteredConstructionCosts(item).length > 0">
                  <tr
                    v-for="(
                      costItem, costIndex
                    ) in getFilteredConstructionCosts(item)"
                    :key="costIndex"
                  >
                    <td class="td-item depth-2">{{ costItem.name }}</td>
                    <td class="td-value">
                      <v-text-field
                        v-if="costItem.modify"
                        v-model="costItem.data"
                        dense
                        variant="plain"
                        type="number"
                        class="input-field"
                        @input="updateValue(costItem, $event)"
                      ></v-text-field>
                      <span v-else class="blue-text">{{ costItem.data }}</span>
                    </td>
                    <td class="td-unit">{{ costItem.unit }}</td>
                  </tr>
                </template>
                <p v-else class="text-center red-text">   </p>
              </template>

              <!--   -->
              <template v-else-if="hasNamedValues(item.values)">
                <tr>
                  <th colspan="3" class="bold-text">
                    {{ item.name }}
                  </th>
                </tr>
                <template
                  v-for="(subItem, subIndex) in item.values"
                  :key="subIndex"
                >
                  <tr>
                    <td class="td-item">
                      {{ subItem.name }}
                    </td>
                    <td class="td-value">
                      <v-text-field
                        v-if="subItem.modify"
                        v-model="subItem.data"
                        dense
                        variant="plain"
                        type="number"
                        class="input-field"
                      ></v-text-field>
                      <span v-else class="blue-text">
                        {{ subItem.data }}
                      </span>
                    </td>
                    <td class="td-unit">{{ subItem.unit }}</td>
                  </tr>
                </template>
              </template>

              <!--   -->
              <!--     -->
            </template>
          </tbody>
        </v-table>
        <p v-else class="text-center"> </p>
      </v-card-text>

      <v-card-actions>
        <v-btn color="primary" @click="save"></v-btn>
        <v-btn color="grey" @click="closeDialog"></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>


<script setup>
import { ref, watch, defineProps, defineEmits, nextTick } from "vue";
import { useDropZoneStore } from "../store/dropZoneStore";
import { useTopologyStore } from "../store/topologyStore";

const topologyStore = useTopologyStore();

const dropZoneStore = useDropZoneStore();

const props = defineProps({
  modelValue: Boolean,
  label: String,
  id: Number,
  uniqueId: String,
});

const emit = defineEmits(["update:modelValue", "saveData"]);

const dropZones = ref([]);
const dialog = ref(false);
const topologyData = ref([]);
const expansionType = ref("");
const powerType = ref("AC");
const solarConstructionType = ref("");

const updateValue = (item, index, event) => {
  if (!event || !item.values[index]) return;

  //     Vue    
  const newValues = [...item.values];
  newValues[index] = {
    ...newValues[index],
    data: event.target ? event.target.value : event,
  };

  item.values = newValues; //        
};

//  `uniqueId`    
watch(
  () => props.uniqueId,
  async (newVal) => {
    if (newVal) {
      console.log("  ,   ");

      //         
      await nextTick(); //  Vue   

      let data = dropZoneStore.getProperty(newVal);
      if (data && data.property) {
        //  `calculateDepth`       
        topologyData.value = JSON.parse(
          JSON.stringify(calculateDepth(data.property))
        );
      }

      console.log(
        "   topologyData:",
        JSON.stringify(topologyData.value)
      );
    }
  },
  { immediate: true, deep: false }
);

//  `values`   "name"    
const hasNamedValues = (values) => {
  return (
    Array.isArray(values) &&
    values.length > 0 &&
    values.some(
      (val) =>
        val.name && typeof val.name === "string" && val.name.trim() !== ""
    )
  );
};

//  `modelValue`     
watch(
  () => props.modelValue,
  (newVal) => {
    dialog.value = newVal;
  }
);

//   
const closeDialog = () => {
  emit("update:modelValue", false);

  //   
  topologyData.value = [];
};

const isSimpleArray = (values) => {
  return Array.isArray(values) && values.length > 0 && !values[0].name;
};

watch(
  () => dropZoneStore.dropZones,
  (newData) => {
    if (newData.length > 0) {
      dropZones.value = JSON.parse(JSON.stringify(newData)); //      
    }
  },
  { deep: true, immediate: true }
);

//    (costs     )
const save = async () => {
  const targetZoneIndex = dropZones.value.findIndex((zone) =>
    zone.items.some((subItem) => subItem.uniqueId === props.uniqueId)
  );

  if (targetZoneIndex === -1) {
    console.error("  uniqueId  Zone   ");
    return;
  }

  const targetItemIndex = dropZones.value[targetZoneIndex].items.findIndex(
    (subItem) => subItem.uniqueId === props.uniqueId
  );

  if (targetItemIndex === -1) {
    console.error("  uniqueId  Item   ");
    return;
  }

  //     ( )
  const targetItem = JSON.parse(
    JSON.stringify(dropZones.value[targetZoneIndex].items[targetItemIndex])
  );

  //   property    
  const updatedProperty = topologyData.value.map((prop) => {
    if (prop.name === "  " || prop.name === " ") {
      const filteredCosts = getFilteredConstructionCosts(prop);

      return {
        name: prop.name,
        values:
          filteredCosts.length > 0
            ? [
                {
                  constructionType:
                    prop.name === "  "
                      ? expansionType.value
                      : solarConstructionType.value,
                  ...(prop.name === "  " && {
                    powerType: powerType.value,
                  }),
                  costs: filteredCosts.map((cost) => ({
                    name: cost.name || " ",
                    data: Number(cost.data) || 0,
                    unit: cost.unit || "",
                    modify: cost.modify ?? true,
                  })),
                },
              ]
            : [],
      };
    }

    return {
      name: prop.name,
      values: prop.values.map((val, index, array) => ({
        ...(array.length > 1 ? { name: val.name || prop.name } : {}),
        data: Number(val.data) ?? 0,
        unit: val.unit || "",
        modify: val.modify ?? true,
      })),
    };
  });

  //  depth 
  updatedProperty.forEach((prop) => {
    delete prop.depth;
    if (prop.values) {
      prop.values.forEach((val) => {
        delete val.depth;
      });
    }
  });

  //    
  targetItem.property = updatedProperty;

  const sharedPropNames = [" ", " "];
  const sharedProps = updatedProperty.filter((prop) =>
    sharedPropNames.includes(prop.name)
  );
  const originalProperty =
    dropZones.value[targetZoneIndex].items[targetItemIndex].property;

  if (String(targetItem.label).includes("154")) {
    //   154   
    dropZones.value.forEach((zone) => {
      zone.items.forEach((item) => {
        if (String(item.label).includes("154")) {
          if (item.uniqueId === props.uniqueId) {
            item.property = JSON.parse(JSON.stringify(updatedProperty));
          } else {
            const newProperty = item.property.map((prop) => {
              const matched = sharedProps.find((p) => p.name === prop.name);
              return matched ? JSON.parse(JSON.stringify(matched)) : prop;
            });
            item.property = newProperty;
          }
          item.setProperty = true;
        }
      });
    });
  } else {
    //  154     item 
    const hasChanges =
      JSON.stringify(updatedProperty) !== JSON.stringify(originalProperty);

    if (hasChanges) {
      dropZones.value[targetZoneIndex].items[targetItemIndex].property =
        JSON.parse(JSON.stringify(updatedProperty));
      dropZones.value[targetZoneIndex].items[
        targetItemIndex
      ].setProperty = true;
    } else {
      //      setProperty true
      dropZones.value[targetZoneIndex].items[targetItemIndex].property =
        JSON.parse(JSON.stringify(updatedProperty));
      dropZones.value[targetZoneIndex].items[
        targetItemIndex
      ].setProperty = true;
    }
  }

  //  dropZoneStore  ( )
  dropZoneStore.dropZones = JSON.parse(JSON.stringify(dropZones.value));
  updateTopologyListProperty(targetItem);

  emit("saveData", JSON.parse(JSON.stringify(dropZones.value)));

  await nextTick(); //  UI    
  closeDialog();
};

const updateTopologyListProperty = (updatedItem) => {
  // topologyList     
  for (const zone of topologyStore.topologyList) {
    for (const item of zone) {
      if (item.uniqueId === updatedItem.uniqueId) {
        item.property = JSON.parse(JSON.stringify(updatedItem.property));
        item.setProperty = true;
        console.log("");
        return; //   
      }
    }
  }
};

const calculateDepth = (data, depth = 1) => {
  return data.map((item) => {
    let newItem = {
      ...JSON.parse(JSON.stringify(item)), //  
      depth,
    };

    //  values    
    if (Array.isArray(newItem.values)) {
      newItem.values = newItem.values.map((val) => {
        let newVal = {
          ...JSON.parse(JSON.stringify(val)),
          depth: depth + 1, //  values depth 
        };

        //  values  `costs`    depth 
        if (Array.isArray(newVal.costs)) {
          newVal.costs = newVal.costs.map((cost) => ({
            ...JSON.parse(JSON.stringify(cost)),
            depth: newVal.depth + 1, //  `costs` values depth 1  
          }));
        }

        return newVal;
      });
    } else {
      newItem.values = [];
    }

    return newItem;
  });
};

//        ( NEW )
const isConstructionCost = (item) => {
  return item.name === "  " || item.name === " ";
};

//       ( 229 +    )
const getFilteredConstructionCosts = (item) => {
  if (!item || !item.values) return [];

  if (item.name === "  ") {
    return (
      item.values.find(
        (cost) =>
          cost.constructionType === expansionType.value &&
          cost.powerType === powerType.value
      )?.costs || []
    );
  }

  if (item.name === " ") {
    return (
      item.values.find(
        (cost) => cost.constructionType === solarConstructionType.value
      )?.costs || []
    );
  }

  return [];
};
</script>


<style scoped lang="scss">
.dialog-title {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  padding: 12px;
}

.dialog-title {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  padding: 12px;
  color: white;
  background-color: #1b67ca;
  border-radius: 6px 6px 0 0;
}

/*    */
.styled-table {
  width: 100%;
  border-collapse: collapse;
}

/*     */
th {
  background-color: #ececec !important;
  font-weight: bold !important;
  padding: 10px;
  text-align: center !important;
}

/*  Depth-1 Bold  */
.td-item.depth-1,
th.depth-1 {
  font-weight: bold;
  background-color: #f8f9fc;
}

/*  Depth-2, Depth-3   */
.td-item {
  &.depth-2 {
    padding-left: 20px;
    font-weight: normal;
  }
  &.depth-3 {
    padding-left: 32px;
    font-weight: lighter;
  }
}

/*     */
.input-field {
  width: 80%;
  min-width: 100px;
  height: 36px;
  font-size: 16px;
  text-align: center;
  padding: 0 10px;
  margin: 0 auto;
  border: none;
  border-bottom: 1px solid #ccc;
  background: transparent;
  transition: border-color 0.2s ease-in-out;

  &:focus-within {
    border-bottom: 2px solid #004c99 !important;
  }

  /*  Vuetify    */
  ::v-deep(.v-input__control) {
    border: none !important;
    box-shadow: none !important;
    background: none !important;
    min-height: 36px;
    padding: 0;
  }

  ::v-deep(.v-input__control input) {
    text-align: center; /*      */
    padding: 0;
    border: none !important;
    background: none !important;
    outline: none;
    box-shadow: none;
    font-weight: bold;
    color: #333;
  }
}

::v-deep(.v-selection-control-group) {
  flex-direction: unset !important;
  justify-content: flex-start !important;
  align-items: center !important;
  width: 30% !important;
}

.td-value {
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-weight: bold;
  font-size: 15px;
}
</style>