<template>
  <div class="step-container">
    <v-container class="plot-area">
      <!--    -->

      <v-tabs v-model="selectedTab" centered>
        <v-tab value="cost">   </v-tab>
        <v-tab value="carbon"></v-tab>
        <v-tab value="roi">ROI</v-tab>
      </v-tabs>

      <v-row justify="end" align="center">
        <v-col cols="auto">
          <v-select
            v-model="selectedGroupIndex"
            :items="groupOptions"
            item-title="title"
            item-value="value"
            label=" "
            density="compact"
            variant="outlined"
            style="max-width: 250px"
            class="mb-0"
            hide-details
          />
        </v-col>

        <v-btn @click="exportAllToPDF" color="primary">   </v-btn>
      </v-row>

      <!--    -->
      <v-window v-model="selectedTab" class="chartContent">
        <!--      -->
        <v-window-item value="cost">
          <v-row>
            <v-col cols="6">
              <h3> </h3>
              <canvas ref="barChart"></canvas>
            </v-col>
            <v-col cols="6">
              <h3> </h3>
              <canvas ref="doughnutChart"></canvas>
            </v-col>
          </v-row>
        </v-window-item>

        <!--    -->
        <v-window-item value="carbon">
          <v-row>
            <v-col cols="12">
              <h3>  </h3>
              <canvas ref="carbonChart"></canvas>
            </v-col>
          </v-row>
        </v-window-item>

        <!-- ROI  -->
        <v-window-item value="roi">
          <v-row>
            <v-col cols="12">
              <h3>ROI </h3>
              <canvas ref="roiChart"></canvas>
            </v-col>
          </v-row>
        </v-window-item>
      </v-window>
    </v-container>

    <v-container class="table-container">
      <v-card class="custom-card h-auto">
        <v-data-table
          :items="tableItems"
          class="custom-table"
          hide-default-footer
          tabindex="-1"
        >
        </v-data-table>
      </v-card>
    </v-container>
  </div>

  <div ref="pdfArea" class="pdf-export-area">
    <h1>NWAs      </h1>

    <hr />
    <p class="summary">Summary</p>
    <p>
       (
      <strong
        >{{ map_topologyList[0].get(""). }}, lat :
        {{ map_topologyList[0].get("").lat }}, lng :
        {{ map_topologyList[0].get("").lng }}
      </strong>
      )  
      <strong> {{ map_topologyList[0].get(""). }}</strong> MW 
       NWAs     
    </p>

    <section>
      <h3>1.    </h3>
      <p>
           (TLC<sup>A</sup>), 
        (TC<sup>B</sup>),  (TLR<sup>C</sup>)  
        (PTC<sup>D</sup>)  .
      </p>

      <div class="expression">
        <p>
          <strong>A)</strong> TLC = P<sub>d</sub>  <sub>i</sub> C<sub
            >L<sub>i</sub></sub
          >L<sub>p,i</sub>
        </p>
        <p><strong>B)</strong> TC = P<sub>d</sub>  C<sub>TC</sub>  j</p>
        <p><strong>C)</strong> TLR = P<sub>d</sub>  L<sub>TLR</sub></p>
        <p><strong>D)</strong> PTC = P<sub>d</sub>  P<sub>E</sub></p>
      </div>
    </section>

    <v-data-table
      :items="tableItems"
      class="custom-table export-table"
      hide-default-footer
      tabindex="-1"
    />

    <div
      v-for="(group, idx) in topologyList"
      :key="idx"
      style="page-break-after: auto"
    >
      <h3>{{ idx + 2 }}.  {{ idx + 1 }}  </h3>

      <div class="export-plot">
        <div class="export-bar">
          <h3> </h3>
          <canvas
            :ref="(el) => renderAllBarChart(el, idx)"
            width="300"
            height="250"
            calss="barPlot"
          />
        </div>
        <div class="export-doughnut">
          <h3> </h3>
          <canvas
            :ref="(el) => renderAllDoughnutChart(el, idx)"
            width="400"
            height="250"
            class="doughnutPlot"
          />
        </div>
      </div>
    </div>
  </div>
</template>




<script setup>
import { ref, onMounted, nextTick, watch, computed } from "vue";
import html2pdf from "html2pdf.js";
import { storeToRefs } from "pinia";
import Chart from "chart.js/auto";
import { useTopologyStore } from "../store/topologyStore.js";

const topologyStore = useTopologyStore();

const { topologyList } = storeToRefs(topologyStore);

const arrayToMap = (topologyList) => {
  return topologyList.map((route) => {
    const lossMap = {};

    const node154 = route.find((n) => n.label === "154");
    if (node154?.property) {
      const  = node154.property.find((p) => p.name === " ");
      if (?.values) {
        .values.forEach((v) => {
          if (v.name && v.data !== undefined) {
            lossMap[v.name] = v.data;
          }
        });
      }
    }

    const entries = route.map((node) => {
      const label = node.label;
      const normalizedLabel =
        label !== "" ? label.replace(/-[A-Z]$/i, "") : label;

      const props = node.property ?? [];

      const formatted = {};

      props.forEach((prop) => {
        const name = prop.name;
        const values = prop.values;

        if (!name || !Array.isArray(values)) return;

        // 1.  :  
        if (values.length === 1 && "data" in values[0]) {
          formatted[name] = values[0].data;
        }

        // 2. {name, data}   
        else if (values.every((v) => "name" in v && "data" in v)) {
          formatted[name] = values.reduce((obj, v) => {
            obj[v.name] = v.data;
            return obj;
          }, {});
        }

        // 3.   (ex:   )
        else if (
          ["  ", "  "].includes(name)
        ) {
          formatted[name] = values.map((v) => ({
            ...(v.constructionType && { constructionType: v.constructionType }),
            ...(v.powerType && { powerType: v.powerType }),
            costs: Array.isArray(v.costs)
              ? v.costs.reduce((obj, item) => {
                  obj[item.name] = item.data;
                  return obj;
                }, {})
              : {},
          }));
        }

        // 4.   
        else {
          formatted[name] = values;
        }
      });

      if (lossMap[label] !== undefined) {
        formatted[""] = lossMap[label];
      }

      formatted["lat"] = node.lat ?? 0;
      formatted["lng"] = node.lng ?? 0;

      return [normalizedLabel, formatted];
    });
    return new Map(entries);
  });
};

const map_topologyList = arrayToMap(topologyList.value);

//    
const selectedTab = ref("cost");

//   
const barChart = ref(null);
const doughnutChart = ref(null);
const carbonChart = ref(null);
const roiChart = ref(null);

//  Chart.js   
let barChartInstance = null;
let doughnutChartInstance = null;
let carbonChartInstance = null;
let roiChartInstance = null;

const tableItems = ref([]);
const pdfArea = ref(null);

//  3.  
const chartDataList = ref([]); //     { bar, doughnut, table }
const barDataValues = ref([{}]);

const doughnutDataValues = ref({
  labels: [],
  datasets: [
    {
      label: "NWAs  ",
      data: [],
      backgroundColor: [],
      borderWidth: 1,
    },
  ],
});

const selectedGroupIndex = ref(0); //    

//    (ex: [" 0", " 1", ...])
const groupOptions = computed(() =>
  topologyList.value.map((_, idx) => ({
    title: ` ${idx + 1}`,
    value: idx,
  }))
);

const updateCostView = () => {
  const group = map_topologyList[selectedGroupIndex.value];
  if (!group) return;

  const result = calCost(group);

  barDataValues.value = result.bar;
  doughnutDataValues.value = result.doughnut;
  tableItems.value = result.table;
};

//    
const createCharts = async () => {
  await nextTick(); //  DOM    
  destroyCharts(); //    ( )

  //     
  if (barChart.value) {
    try {
      barChartInstance = new Chart(barChart.value.getContext("2d"), {
        type: "bar",
        data: barDataValues.value,
        options: {
          responsive: true,
          maintainAspectRatio: false,
          plugins: {
            legend: {
              display: true,
            },
            tooltip: {
              callbacks: {
                label: function (context) {
                  const value = context.parsed.y || 0;
                  return (
                    context.dataset.label +
                    ": " +
                    (value / 1_000_000).toLocaleString() +
                    "  "
                  );
                },
              },
            },
          },
          scales: {
            y: {
              beginAtZero: true,
              title: {
                display: true,
                text: " ()",
              },
              ticks: {
                callback: function (value) {
                  return (value / 1_000_000).toLocaleString() + "";
                },
              },
            },
          },
        },
      });
    } catch (err) {
      console.error("chart ");
    }
  }

  //     
  if (doughnutChart.value) {
    doughnutChartInstance = new Chart(doughnutChart.value.getContext("2d"), {
      type: "doughnut",
      data: doughnutDataValues.value,
      options: {
        responsive: true,
        maintainAspectRatio: false,
        plugins: {
          legend: { position: "top" },
          tooltip: {
            callbacks: {
              label: function (context) {
                const value = context.raw;
                const total = context.dataset.data.reduce(
                  (sum, v) => sum + v,
                  0
                );
                const percent = ((value / total) * 100).toFixed(2);

                const valueInMillion = (value / 1_000_000).toLocaleString();

                return `${context.label}: ${valueInMillion}   (${percent}%)`;
              },
            },
          },
        },
      },
      plugins: [centerTextPlugin], //  !
    });
  }

  //    
  if (carbonChart.value) {
    carbonChartInstance = new Chart(carbonChart.value.getContext("2d"), {
      type: "line",
      data: {
        labels: ["2020", "2025", "2030", "2035"],
        datasets: [
          {
            label: "",
            data: [100, 80, 50, 30],
            borderColor: "#E63946",
            backgroundColor: "rgba(230, 57, 70, 0.2)",
            fill: true,
          },
        ],
      },
      options: { responsive: true, maintainAspectRatio: false },
    });
  }

  //  ROI 
  if (roiChart.value) {
    roiChartInstance = new Chart(roiChart.value.getContext("2d"), {
      type: "bar",
      data: {
        labels: ["2020", "2025", "2030", "2035"],
        datasets: [
          {
            label: "ROI",
            data: [1.2, 1.5, 2.0, 2.8],
            backgroundColor: "#264653",
          },
        ],
      },
      options: { responsive: true, maintainAspectRatio: false },
    });
  }
};

//  
const calCost = (group) => {
  //  =   - NWAs 

  //   = tlc + tc + tlr + ptc
  let tlc = getTLC(group); //  
  let tc = getTC(group); //  
  let tlr = getTLR(group); //  
  let ptc = 0; //  

  let dlc = getDLC(group);

  let NWAs = getNwasCostTotal(group);

  // console.log("tlc : ", tlc);
  // console.log("tc : ", tc);
  // console.log("tlr : ", tlr);
  // console.log("ptc : ", ptc);
  // console.log(" : " + dlc);
  // console.log("NWAs : ", NWAs);

  // for (const [label, props] of group.entries()) {
  //   console.log(":", label, ":", props);
  // }

  const bar = getBarChartData({ tlc, tc, tlr, ptc, dlc, group });
  const doughnut = getDoughnutChartData(group, NWAs);
  const table = createCostBenefitTableForGroup(map_topologyList);

  return { bar, doughnut, table }; // ! ref  X
};

//   
const getTLC = (group) => {
  const getCost = (label) => group.get(label)?.[" "] ?? 0;
  const getLatLng = (label) =>
    group.get(label) ? [group.get(label).lat, group.get(label).lng] : null;

  let tlc = 0;

  // 765  345
  const latlng765 = getLatLng("765");
  const latlng345 = getLatLng("345");
  if (latlng765 && latlng345) {
    const dist = getDistance(...latlng765, ...latlng345);
    const cost = getCost("765");
    tlc += dist * cost * 1_000_000;
  }

  // 345  154 (  )
  const latlng154List = [...group]
    .filter(([label]) => label === "154")
    .map(([, data]) => [data.lat, data.lng]);

  if (latlng345 && latlng154List.length > 0) {
    const cost = getCost("345");
    latlng154List.forEach(([lat, lng]) => {
      const dist = getDistance(...latlng345, lat, lng);
      tlc += dist * cost * 1_000_000;
    });
  }

  // 154  154  22.9 ( )
  const latlng229 = getLatLng("22.9");
  const cost154 = getCost("154");

  for (let i = 0; i < latlng154List.length - 1; i++) {
    const [lat1, lng1] = latlng154List[i];
    const [lat2, lng2] = latlng154List[i + 1];
    const dist = getDistance(lat1, lng1, lat2, lng2);
    tlc += dist * cost154 * 1_000_000;
  }

  //  154  22.9
  if (latlng229 && latlng154List.length > 0) {
    const [lat1, lng1] = latlng154List[latlng154List.length - 1];
    const dist = getDistance(lat1, lng1, ...latlng229);
    tlc += dist * cost154 * 1_000_000;
  }

  return Math.round(tlc);
};

const getTC = (group) => {
  const demandNode = group.get("");
  const rateNode = group.get("154");

  if (!demandNode || !rateNode) return 0;

  // 1.  (MW  kW)
  const demandKW = Number(demandNode[""] ?? 0) * 1000;

  // 2.  
  const usageHoursPerDay = Number(
    (demandNode["  "] ?? "0").trim()
  );

  // 3.   ( )
  const  = rateNode[" "];
  if (!) return 0;

  const  = Number([" "] ?? 0);
  const  = Number([" "] ?? 0);

  // 4. 
  const tc = demandKW *  * usageHoursPerDay * 365 + demandKW * ;

  return Math.round(tc); //  
};

const getTLR = (group) => {
  const firstLabel = group.keys().next().value;
  const demandNode = group.get("");
  const rateNode = group.get("154");

  if (!demandNode || !rateNode) return 0;

  const  = rateNode[" "];
  if (!) return 0;

  const  = Number([firstLabel] ?? 0);
  const  = Number(demandNode[""] ?? 0); // MW
  const  = Number(demandNode["  "] ?? "0");

  const tlr =  *  *  * 365;

  return Math.round(tlr); //  
};

const getDLC = (group) => {
  const node229 = group.get("22.9");
  const demandNode = group.get("");

  if (!node229 || !demandNode) return 0;

  const distance = getDistance(
    node229.lat,
    node229.lng,
    demandNode.lat,
    demandNode.lng
  );
  const costsObj = node229["  "]?.[0]?.costs;

  const totalUnitCost =
    Object.values(costsObj).reduce((sum, value) => {
      const number = Number(value) || 0;
      return sum + number;
    }, 0) * 10000; //   

  const dlc = distance * totalUnitCost;

  return Math.round(dlc);
};

const getNwasCostTotal = (group) => {
  let nwasCosts = [];

  for (const [label, node] of group.entries()) {
    if (
      !["", "", ""].some((prefix) => label.startsWith(prefix))
    )
      continue;

    const type = label.startsWith("")
      ? ""
      : label.startsWith("")
      ? ""
      : "";

    const costKey = ' '

    const  = node[costKey]?.[0];
    if (! || !Array.isArray(.costs)) continue;

    const { constructionType, costs } = ;
    const isNew = constructionType === "";
    const capacityKey = type === "" ? "" : "";
    const  = Number(node[capacityKey] ?? 0);

    //   Map  
    const costMap = {};
    costs.forEach((item) => {
      if (item.name && item.data != null) {
        costMap[item.name] = Number(item.data) || 0;
      }
    });

    let  = 0;

    if (type === "") {
      const  = costMap[""] ?? 0;
      const  = costMap[""] ?? 0;
      const  = costMap[""] ?? 0;
      const  = costMap["   (SMP)"] ?? 0;
      const  =  * ;

       = isNew
        ?  +  +  + 
        :  +  + ;
    } else if (type === "" || type === "") {
      const  = costMap["  "] ?? 0;
      const  = costMap["/"] ?? 0;
      const  = costMap[" "] ?? 0;
      const  = costMap[" "] ?? 0;
      const  = costMap["  "] ?? 0;
      const  = costMap[""] ?? 0;
      const  =  * ;

       = isNew
        ?  +  +  +  +  + 
        :  +  + ;
    }

    nwasCosts.push({ type, label, cost: Math.round( * 1000) }); //   
  }

  console.log('NWAsGroup : ', group);
  console.log('MWAs : ', nwasCosts)

  return nwasCosts;
};

const getDistance = (lat1, lng1, lat2, lng2) => {
  const R = 6371e3; //   ()
  const rad = Math.PI / 180;

  const 1 = lat1 * rad;
  const 2 = lat2 * rad;
  const  = (lat2 - lat1) * rad;
  const  = (lng2 - lng1) * rad;

  const a =
    Math.sin( / 2) ** 2 + Math.cos(1) * Math.cos(2) * Math.sin( / 2) ** 2;
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));

  return (R * c) / 1000; // km 
};

const destroyCharts = () => {
  if (barChartInstance) {
    barChartInstance.destroy();
    barChartInstance = null;
  }
  if (doughnutChartInstance) {
    doughnutChartInstance.destroy();
    doughnutChartInstance = null;
  }
  if (carbonChartInstance) {
    carbonChartInstance.destroy();
    carbonChartInstance = null;
  }
  if (roiChartInstance) {
    roiChartInstance.destroy();
    roiChartInstance = null;
  }
};

//    
const getBarChartData = ({ tlc, tc, tlr, ptc = 0, dlc, group }) => {
  const nwasList = getNwasCostTotal(group); // [{type, label, cost}, ...]

  const labels = ["", "", ...nwasList.map((n) => n.type)];
  const data = [tlc + tc + tlr + ptc, dlc, ...nwasList.map((n) => n.cost)];
  const backgroundColor = [
    "#4285F4", // 
    "#FB8C00", // 
    ...nwasList.map((n) => {
      if (n.type === "") return "#9E9E9E";
      if (n.type === "") return "#4CAF50";
      if (n.type === "") return "#00ACC1";
      return "#CCCCCC";
    }),
  ];

  return {
    labels,
    datasets: [
      {
        label: " ",
        data,
        backgroundColor,
      },
    ],
  };
};

const getDoughnutChartData = (group, nwases) => {
  const totalInfraCost =
    getTLC(group) + getTC(group) + getTLR(group) + getDLC(group);

  if (!nwases || typeof nwases !== "object") {
    console.warn(" NWAs  :", nwases);
    return {
      labels: [],
      datasets: [{ label: "NWAs ", data: [], backgroundColor: [] }],
    };
  }

  const data = Object.entries(nwases).map(([, nwas]) => {
    const name = nwas.label || nwas.name || " ";
    const benefit = totalInfraCost - (nwas.cost ?? 0); //  !!!
    return {
      name,
      benefit: isNaN(benefit) ? 0 : benefit,
    };
  });

  return {
    labels: data.map((d) => d.name),
    datasets: [
      {
        label: "NWAs  ",
        data: data.map((d) => d.benefit),
        backgroundColor: [
          "#9CA3AF",
          "#FACC15",
          "#4BC0C0",
          "#FF6384",
          "#36A2EB",
        ].slice(0, data.length),
        borderWidth: 1,
      },
    ],
  };
};

const centerTextPlugin = {
  id: "centerText",
  beforeDraw(chart) {
    const { width, height, ctx } = chart;
    ctx.save();

    const labels = chart.data.labels;
    const data = chart.data.datasets[0].data;

    ctx.font = "bold 14px sans-serif";
    ctx.fillStyle = "#333";
    ctx.textAlign = "center";
    ctx.textBaseline = "middle";

    const lines = labels.map((label, i) => {
      const value = data[i];
      const valueInMillion = (value / 1_000_000).toLocaleString();
      return `${label}: ${valueInMillion}  `;
    });

    //       
    const lineHeight = 20;
    const offset = (lines.length - 1) * -0.5 * lineHeight;

    lines.forEach((line, idx) => {
      ctx.fillText(line, width / 2, height / 2 + offset + idx * lineHeight);
    });

    ctx.restore();
  },
};

const createCostBenefitTableForGroup = (map_topologyList) => {
  return map_topologyList.map((group, idx) => {
    const tlc = getTLC(group);
    const tc = getTC(group);
    const tlr = getTLR(group);
    const ptc = 0;
    const dlc = getDLC(group);
    const NWAs = getNwasCostTotal(group);

    const totalInfraCost = tlc + tc + tlr + ptc + dlc;

    const nwasTotalCost = Object.values(NWAs).reduce(
      (sum, item) => sum + (item.cost ?? item.value ?? 0),
      0
    );

    const totalBenefit = totalInfraCost - nwasTotalCost;

    return {
      : ` ${idx + 1}`,
      _: totalInfraCost.toLocaleString() + "",
      NWAs_: nwasTotalCost.toLocaleString() + "",
      NWAs_: totalBenefit.toLocaleString() + "",
    };
  });
};

const exportAllToPDF = async () => {
  chartDataList.value = [];

  map_topologyList.forEach((group) => {
    if (!group) return;
    const result = calCost(group);
    chartDataList.value.push(result);
  });

  await nextTick();

  const element = pdfArea.value;
  element.style.display = "block";

  //    (chart canvas   )
  // await new Promise((res) => setTimeout(res, 500)); // 500ms 

  await html2pdf()
    .set({
      margin: 0.5,
      filename: "__.pdf",
      image: { type: "jpeg", quality: 0.98 },
      html2canvas: { scale: 2 },
      jsPDF: { unit: "in", format: "a4", orientation: "portrait" },
    pagebreak: { mode: ['css', 'legacy'] }, //    
    })
    .from(element)
    .save();

  // element.style.display = "none";
};

const barChartInstances = [];
const doughnutChartInstances = [];

const renderAllBarChart = (canvasEl, idx) => {
  if (!canvasEl || !chartDataList.value[idx]) return;

  //   
  if (barChartInstances[idx]) {
    barChartInstances[idx].destroy();
  }

  const ctx = canvasEl.getContext("2d");

  barChartInstances[idx] = new Chart(ctx, {
    type: "bar",
    data: chartDataList.value[idx].bar,
    options: {
      responsive: false,
      animation: false,
      plugins: { legend: { display: false } },
      scales: {
        y: {
          beginAtZero: true,
          title: {
            display: true,
            text: " ()",
          },
          ticks: {
            callback: function (value) {
              return (value / 1_000_000).toLocaleString() + "";
            },
          },
        },
      },
    },
  });
};

const renderAllDoughnutChart = (canvasEl, idx) => {
  if (!canvasEl || !chartDataList.value[idx]) return;

  if (doughnutChartInstances[idx]) {
    doughnutChartInstances[idx].destroy();
  }

  const ctx = canvasEl.getContext("2d");
  doughnutChartInstances[idx] = new Chart(ctx, {
    type: "doughnut",
    data: chartDataList.value[idx].doughnut,
    options: {
      responsive: false,
      animation: false,
      plugins: { legend: { position: "bottom" } },
    },
  });
};

//  onMounted  
onMounted(() => {
  const group = map_topologyList[selectedGroupIndex.value];
  const result = calCost(group);

  barDataValues.value = result.bar;
  doughnutDataValues.value = result.doughnut;
  tableItems.value = result.table;

  createCharts();
});

//       
watch(selectedTab, createCharts);

watch(selectedGroupIndex, async () => {
  await nextTick(); //  DOM/  

  if (selectedGroupIndex.value == null) return;

  updateCostView();
  createCharts();
});
</script>


